import { ToastService } from './../../../_services/_toast/_toast.service';
import { Component, OnInit } from '@angular/core';
import { DbService } from 'src/app/_services';

@Component({
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css'],
})
export class DashboardComponent implements OnInit {
  constructor(private db: DbService, private toastService: ToastService) {}

  invoicesThisMonth = 0;
  currentMonth = '';

  ngOnInit() {
    this.SetCurrentMonth();
    this.GetCurrentMonthInvoices();
  }

  private SetCurrentMonth() {
    const today = new Date();
    this.currentMonth = today.toLocaleString('default', { month: 'long' });
  }

  private async GetCurrentMonthInvoices() {
    try {
      const invoices = await this.db.getInvoices();
      this.invoicesThisMonth = this.GetTotalCurrentMonthInvoices(invoices);
    } catch (error) {
      this.toastService.showError(error);
    }
  }

  private GetTotalCurrentMonthInvoices(invoices: any): number {
    const currentMonthsInvoices: Date[] = [];
    const today = new Date();
    invoices.forEach((invoice) => {
      const invoiceDate = new Date(invoice.invoiceDate);
      if (invoiceDate.getMonth() === today.getMonth()) {
        currentMonthsInvoices.push(invoiceDate);
      }
    });
    return currentMonthsInvoices.length;
  }
}
